
#features{
    /* width:100%;
    height:100vh; */
    box-sizing: border-box;
    font-family: calibri;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    padding-top: 40px ;
    padding-bottom: 40px ;
    margin: 0%;
    background-color: #ffffff;
  }
  .a-container{
    display: flex;
    justify-content: center;
    align-content: center;
  
  
  }
  .a-box{
    background-color: #ffffff;
    width:300px;
    height: 450px;
    margin: 0 20px;
    border-radius: 10px;
    overflow: hidden;
    box-shadow: 0px 10px 12px rgba(0,0,0,0.1);
    transition: 0.3s ease-in-out;
    position: relative;
  }
  .a-b-img{
    width: 100%;
    height: 60%;
  
  }
  .a-b-img img{
    padding: 15px;
    width:100%;
    height: 100%;
  
  }
  .a-box:hover{
    box-shadow: 2px 2px 12px rgba(0,0,0,0.1);
  }